@import url("https://use.typekit.net/gbs7leh.css");
* {
  font-family: proxima-soft, sans-serif;
  font-style: normal;
  font-weight: 300;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 600;
}

button.on-off {
  /* background: $--color-one;
  border: solid $--color-one; */
  border-style: solid;
  border-bottom-left-radius: 1rem;
  border-top-left-radius: 1rem;
  bottom: 0;
  color: #FFFFFF;
  cursor: pointer;
  font-size: x-large;
  padding: 0.2rem 2rem 0.2rem 1rem;
  position: fixed;
  right: -1%;
  z-index: 2;
}

.copyright {
  /* background: $--color-one; */
  border-bottom-left-radius: 1rem;
  border-top-left-radius: 1rem;
  bottom: 3rem;
  color: #FFFFFF;
  max-height: 80%;
  max-width: 20rem;
  overflow: auto;
  padding: 1rem;
  position: fixed;
  right: -160%;
  text-transform: none;
  transition: right 1s;
  z-index: 1;
}

.open {
  right: 0;
}

.copyright ul {
  list-style: none;
  margin: 0 1rem;
}

.slide-top {
  -webkit-animation: slide-top 0.5s ease-in-out 0.2s both;
  animation: slide-top 0.5s ease-in-out 0.2s both;
}

@-webkit-keyframes slide-top {
  0% {
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes slide-top {
  0% {
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
  }
}
.slide-left {
  -webkit-animation: slide-left 0.5s ease-in-out 0.5s both;
  animation: slide-left 0.5s ease-in-out 0.5s both;
}

@-webkit-keyframes slide-left {
  0% {
    -webkit-transform: translateX(100%);
    transform: translateX(100%);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes slide-left {
  0% {
    -webkit-transform: translateX(100%);
    transform: translateX(100%);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
    opacity: 1;
  }
}
.slide-right {
  -webkit-animation: slide-right 0.5s ease-in-out both;
  animation: slide-right 0.5s ease-in-out both;
}

@-webkit-keyframes slide-right {
  0% {
    -webkit-transform: translateX(-100%);
    transform: translateX(-100%);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes slide-right {
  0% {
    -webkit-transform: translateX(-100%);
    transform: translateX(-100%);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
    opacity: 1;
  }
}
.slide-bottom {
  -webkit-animation: slide-bottom 0.5s ease-in-out both;
  animation: slide-bottom 0.5s ease-in-out both;
}

@-webkit-keyframes slide-bottom {
  0% {
    -webkit-transform: translateY(-200%);
    transform: translateY(-200%);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes slide-bottom {
  0% {
    -webkit-transform: translateY(-200%);
    transform: translateY(-200%);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
  }
}
* {
  box-sizing: border-box;
}

.img-magnifier-container {
  position: relative;
}

.img-magnifier-container img {
  width: 100%;
  height: auto;
  max-width: 640px;
}

.img-magnifier-glass {
  position: absolute;
  border-radius: 50%;
  box-shadow: 0 0 0 7px rgba(0, 0, 0, 0.85), 0 0 7px 7px rgba(0, 0, 0, 0.25), inset 0 0 40px 2px rgba(0, 0, 0, 0.25);
  cursor: none;
  width: 200px;
  height: 200px;
  z-index: 1;
}

* {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  text-transform: uppercase;
}

body {
  color: #0c0c0c;
}

body,
p,
h1,
ul,
li,
blockquote,
figure {
  margin: 0;
  padding: 0;
}

h1 {
  font-size: clamp(2em, 2em + 1vw, 1em);
  line-height: 1em;
  /* color: $--color-one; */
}

h2 {
  font-size: clamp(2em, 2em + 1vw, 1em);
  line-height: 1em;
}

strong {
  font-weight: 600;
}

em {
  font-style: italic;
}

header {
  align-items: center;
  display: flex;
  height: 10rem;
  width: 100%;
}

.header__animals {
  background-position: center;
  background-repeat: no-repeat;
  height: 10rem;
  width: 16.25rem;
}

header figure {
  width: 16.25rem;
}

header h1 {
  margin-left: 1rem;
}

main {
  /* text-align: center; */
  display: flex;
  align-items: center;
  flex-direction: column;
}

main p {
  background: #f1f1f1;
  border-radius: 10px;
  margin: 1rem auto;
  max-width: 640px;
  padding: 20px;
  width: 100%;
  text-align: center;
}

video {
  display: flex;
  margin: auto;
  max-width: 45rem;
  width: 100%;
  border: 0 solid;
  border-radius: 1rem;
  position: relative;
}

.container {
  height: 100%;
  width: 100%;
}

.container__content {
  width: 100%;
  max-width: 76.875rem;
  margin: auto;
  padding: 1rem;
}

.footer__copyright {
  text-align: center;
  font-size: small;
}

/*
* Mobile to imagezoom
*/
@media screen and (max-width: 768px) {
  #imagezoom img {
    width: 300px !important;
    height: auto;
  }
}
@media screen and (max-width: 1200px) {
  #imagezoom img {
    width: 600px !important;
    height: auto;
  }
}
@media screen and (max-width: 1600px) {
  #imagezoom img {
    width: 1200px !important;
    height: auto;
  }
}
@media screen and (max-width: 2000px) {
  #imagezoom img {
    width: 1400px !important;
    height: auto;
  }
}
